@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300;400;500;600;700&display=swap');

body {
    font-family: 'Space Grotesk';
    font-size: 17px;
}

h1 {
    font-style: normal;
    font-weight: 400;
    font-size: 64px;
    line-height: 70px;
}

p {
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 22px;
}

@media only screen and (min-width: 1536px) {
    .container {
        max-width: 1216px;
    }
}

.MuiDateRangeCalendar-root>div:first-of-type {
    display: none !important;
}

.bg-sky {
    background-color: #0ea5e9;
}

.bg-warning {
    background-color: #894c0b;
}

.btn {
    min-height: 30px;
}

.btn-primary\/100 {
    background-color: #f2951773;
}

.input:active,
.input:focus {
    outline: none;
}

.textarea:active,
.textarea:focus {
    outline: none;
}

.divider:before,
.divider:after {
    background-color: #b0b0b0 !important;
    height: 0.5px !important;
}

/* data time picker  */

.react-datetime-picker__wrapper {
    border-color: rgb(80, 80, 80) !important;
    border-radius: 8px;
}

.react-datetime-picker__calendar .react-calendar {
    background: #1c1c1c !important;

    border-color: #2c2c2c !important;
}

.react-calendar__navigation button:enabled:hover,
.react-calendar__navigation button:enabled:focus {
    background-color: #f2b705 !important;
}

.react-calendar__tile:enabled:hover,
.react-calendar__tile:enabled:focus {
    background-color: #f2b705 !important;
}

.react-calendar__tile--now {
    background-color: #2c2c2c !important;
}

.react-datetime-picker__inputGroup__amPm {
    background-color: #1c1c1c !important;
}

.react-datetime-picker__clock {
    background-color: #2c2c2c !important;
}

.react-datetime-picker__inputGroup {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

/* tabs */
.tab-active {
    border-color: #f2b705 !important;
}

#nprogress .bar {
    background-color: #f2b705 !important;
    box-shadow: 0 0 2px #f2b705 !important;
}

#nprogress .peg {
    box-shadow: 0 0 10px #f2b705 0 0 5px #f2b705 !important;
}

#nprogress .spinner-icon {
    border-top-color: #f2b705 !important;
    border-left-color: #f2b705 !important;
}

.event-active-bg {
    background: linear-gradient(#5d4b15, #5d4b15) padding-box padding-box,
        linear-gradient(#f2b705, #5d4b15) border-box border-box !important;
}

.event-item:hover {
    background: linear-gradient(#5d4b15, #5d4b15) padding-box padding-box,
        linear-gradient(#f2b705, #5d4b15) border-box border-box !important;
}

.change-telephone .react-tel-input {
    display: flex;
    width: 100%;
    height: 45px;
    justify-content: flex-start;
}

.change-telephone .react-tel-input .form-control {
    background: linear-gradient(rgb(28, 28, 28), rgb(28, 28, 28)) padding-box padding-box,
        linear-gradient(rgb(130, 130, 130), rgb(50, 50, 50)) border-box border-box !important;
    border-radius: 0.75rem !important;
    padding: 24px !important;
    width: 82% !important;
    height: 100% !important;
    font-size: 18px !important;
    margin-left: 18% !important;
    border: 1px solid transparent !important;
}

.change-telephone .react-tel-input .flag-dropdown {
    background: linear-gradient(rgb(28, 28, 28), rgb(28, 28, 28)) padding-box padding-box,
        linear-gradient(rgb(130, 130, 130), rgb(50, 50, 50)) border-box border-box !important;
    width: 15%;
    border: 1px solid transparent !important;
    border-radius: 0.75rem !important;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 24px !important;
}

.change-telephone .react-tel-input .flag-dropdown .selected-flag {
    width: 100%;
}

.react-tel-input .flag-dropdown.open .selected-flag,
.react-tel-input .selected-flag:hover,
.react-tel-input .selected-flag:focus {
    background: #1c1c1c !important;
}

.react-tel-input .country-list {
    background: #1c1c1c !important;
}

.react-tel-input .country-list .country.highlight,
.react-tel-input .country-list .country:hover {
    background: #000000 !important;
}

.register-form .react-tel-input .flag-dropdown {
    background: linear-gradient(rgb(28, 28, 28), rgb(28, 28, 28)) padding-box padding-box,
        linear-gradient(rgb(130, 130, 130), rgb(50, 50, 50)) border-box border-box !important;
    border: 1px solid transparent !important;
}

.register-form .react-tel-input .flag-dropdown .selected-flag:hover {
    background: linear-gradient(rgb(28, 28, 20), rgb(28, 28, 20)) padding-box padding-box,
        linear-gradient(rgb(130, 130, 100), rgb(50, 50, 20)) border-box border-box !important;
    border-radius: 0.75rem !important;
}

.register-form .react-tel-input .form-control {
    width: 100% !important;
    background: linear-gradient(rgb(28, 28, 28), rgb(28, 28, 28)) padding-box padding-box, linear-gradient(rgb(130, 130, 130), rgb(50, 50, 50)) border-box border-box !important;
    border-radius: 0.75rem !important;
    height: 100% !important;
    font-size: 18px !important;
    border: 1px solid transparent !important;
}

.register-form .react-tel-input .country-list {
    width: 230px !important;
}

.peer {
    cursor: auto !important;
}

.peer:focus~.peer-focus\:after\:\!border-white::after,
.peer:focus~.peer-focus\:before\:\!border-white::before {
    border: none !important;
}

.peer:focus~.peer-focus\:leading-tight .child {
    background: #1c1c1c;
    height: 12px;
}

.before\:border-t::before,
.after\:border-t::after {
    border: none !important;
}

.date-time-picker div {
    border-radius: 0.75rem;
    border: 1px solid transparent;
    background: linear-gradient(rgb(28, 28, 28), rgb(28, 28, 28)) padding-box padding-box,
        linear-gradient(rgb(130, 130, 130), rgb(50, 50, 50)) border-box border-box;
    color: white;
}

.date-time-picker label {
    color: white;
}

.date-time-picker div fieldset {
    display: none;
}

.date-time-picker input {
    height: 1rem;
}

.MuiDrawer-paper {
    overflow: unset !important;
}

@media only screen and (min-width: 420px) and (max-width: 770px) {
    h1 {
        font-size: 30px;
        line-height: 33px;
    }

    .change-telephone .react-tel-input .flag-dropdown {
        padding: 3px !important;
    }
}

@media only screen and (max-width: 420px) {
    h1 {
        font-size: 24px;
        line-height: 33px;
    }

    .logo {
        width: 35px;
        height: 35px;
    }

    .text-\[28px\] {
        font-size: 20px;
    }

    .change-telephone .react-tel-input .flag-dropdown {
        padding: 3px !important;
    }
}